    font-size: 1.3rem;
    margin-bottom: 10px;
  }

  .shipyard {
    padding: 12px;
    gap: 12px;
  }

  .ship-token {
    height: 70px;
    padding: 10px;
  }

  .ship-token-container {
    width: 150px;
    height: 75px;
    top: -10%;
  }

  .board-grid {
    grid-template-columns: repeat(10, 35px);
    grid-template-rows: repeat(10, 35px);
  }

  .board-cell {
    width: 35px;
    height: 35px;
  }

  .placement-board,
  .game-board {
    padding: 12px;
  }

  .game-header {
    padding: 15px;
    margin-bottom: 20px;
  }

  .game-header h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
  }

  .game-header h1 .icon {
    position: absolute;
    left: auto;
    width: 70px;
    height: 55px;
  }

  .game-header::before {
    animation: wave-slide 6s ease-in-out infinite;
  }

  .game-header::after {
    top: -150%;
    left: -200%;
    font-size: 1.5rem;
    animation: ship-sail 20s linear infinite;
  }

  #game-status {
    font-size: 1.2rem;
    padding: 12px 20px;
    min-height: 35px;
  }

  .game-boards {
    flex-direction: column;
    gap: 25px;
    padding: 15px;
  }

  .board-title {
    font-size: 1.4rem;
    padding: 10px 20px;
    margin-bottom: 15px;
  }

  .setup-controls,
  .battle-phase > .setup-controls {
    flex-direction: column;
    gap: 12px;
    align-items: center;
  }

  .control-btn,
  .start-btn {
    min-width: 250px;
    width: 90%;
    padding: 14px 20px;
    font-size: 1.1rem;
  }

  .control-btn .icon,
  .start-btn .icon {
    width: 25px;
    height: 22px;
  }

  .modal-content {
    padding: 20px;
    margin: 20px;
  }

  .modal-title {
    font-size: 1.8rem;
  }

  .modal-message {
    font-size: 1.1rem;
  }
}

@media screen and (max-width: 480px) {
  :root {
    --cell-size: 28px;
    --spacing-xs: 3px;
    --spacing-sm: 6px;
    --spacing-md: 10px;
    --spacing-lg: 15px;
    --spacing-xl: 20px;
  }

  .setup-phase {
    padding: 12px 8px;
  }

  .setup-phase h1 {
    font-size: 1.8rem;
    margin-bottom: 10px;
  }

  .setup-phase h1 .icon {
    width: 65px;
    height: 55px;
  }

  .instructions {
    font-size: 0.9rem;
    padding: 6px 10px;
    margin-bottom: 15px;
  }

  .setup-area {
    gap: 15px;
  }

  .shipyard-container h2 {
    font-size: 1.2rem;
  }

  .shipyard {
    padding: 10px;
    gap: 10px;
  }

  .ship-token {
    height: 60px;
    padding: 8px;
  }

  .ship-info {
    font-size: 0.8rem;
  }

  .ship-token-container {
    width: 130px;
    height: 65px;
    top: -15%;
  }

  .board-grid {
    grid-template-columns: repeat(10, 28px);
    grid-template-rows: repeat(10, 28px);
  }

  .board-cell {
    width: 28px;
    height: 28px;
  }

  .board-cell:hover {
    transform: scale(1.05);
  }

  .placement-board,
  .game-board {
    padding: 10px;
  }

  .game-header {
    padding: 12px 8px;
    margin-bottom: 15px;
  }

  .game-header h1 {
    font-size: 2rem;
    letter-spacing: 1px;
    margin-bottom: 8px;
  }

  .game-header h1 .icon {
    width: 65px;
    height: 55px;
  }

  .game-header::before {
    animation: wave-slide 5s ease-in-out infinite;
  }

  #game-status {
    font-size: 1rem;
    padding: 10px 15px;
    min-height: 30px;
    width: 90%;
  }

  .game-boards {
    padding: 12px;
    gap: 20px;
  }

  .board-title {
    font-size: 1.2rem;
    padding: 8px 15px;
    margin-bottom: 12px;
  }

  .board-title::before,
  .board-title::after {
    font-size: 1rem;
  }

  .ship-element {
    border-radius: 3px;
  }

  .setup-controls,
  .battle-phase > .setup-controls {
    gap: 10px;
    margin-top: 15px;
  }

  .control-btn,
  .start-btn {
    min-width: 200px;
    padding: 12px 15px;
    font-size: 1rem;
  }

  .control-btn .icon,
  .start-btn .icon {
    width: 22px;
    height: 20px;
  }

  .modal-content {
    padding: 15px;
    margin: 15px;
  }

  .modal-title {
    font-size: 1.6rem;
  }

  .modal-message {
    font-size: 1rem;
    margin-bottom: 15px;
  }
}

@media screen and (max-width: 320px) {
  :root {
    --cell-size: 24px;
  }

  .setup-phase h1 {
    font-size: 1.5rem;
  }

  .setup-phase h1 .icon {
    width: 55px;
    height: 45px;
  }

  .board-grid {
    grid-template-columns: repeat(10, 24px);
    grid-template-rows: repeat(10, 24px);
  }

  .board-cell {
    width: 24px;
    height: 24px;
  }

  .ship-token-container {
    width: 110px;
    height: 55px;
  }

  .game-header h1 {
    font-size: 1.8rem;
  }

  .game-header h1 .icon {
    width: 55px;
    height: 45px;
  }

  .control-btn,
  .start-btn {
    min-width: 180px;
    font-size: 0.9rem;
    padding: 10px 12px;
  }
}

@media screen and (max-height: 600px) and (orientation: landscape) {
  .setup-area {
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: flex-start;
  }

  .shipyard-container {
    min-width: 200px;
  }

  .shipyard {
    max-height: 350px;
    overflow-y: auto;
  }

  .setup-phase h1 {
    font-size: 1.8rem;
    margin-bottom: 10px;
  }

  .instructions {
    margin-bottom: 10px;
  }
}

@media (hover: none) and (pointer: coarse) {
  .board-cell {
    cursor: pointer;
  }

  .board-cell:hover {
    transform: none;
  }

  .ship-token-container:hover {
    transform: none;
  }

  .board-title:hover {
    transform: none;
  }

  .board-title:hover::before,
  .board-title:hover::after {
    animation: none;
  }

  .control-btn:hover,
  .start-btn:hover {
    transform: none;
    animation: none;
  }

  .board-cell {
    min-width: 28px;
    min-height: 28px;
  }

  .control-btn,
  .start-btn {
    min-height: 44px;
  }
}
